import { Component } from '@angular/core';
import { NavParams, ViewController } from 'ionic-angular';
import { SQLite, SQLiteObject } from "@ionic-native/sqlite";

@Component({
  selector: 'page-categorize-list',
  templateUrl: 'categorize-list.html',
})
export class CategorizeListPage {

  a: string;
  categorie: string;
  items: Array<{name: string, icon: string}>;
  db: SQLiteObject;

  constructor(public params: NavParams, public viewCtrl: ViewController,
              public sqlite: SQLite) {
    this.categorie = params.get('categorie');
    this.sqlite.create({
      name: 'restaurants.db',
      location: 'default'
    })
      .then(db => {
        this.db = db;
        this.SelectList();
      });
  }

  SelectList() {
    this.db.executeSql("SELECT * FROM restaurant WHERE Categorie=\""+this.categorie+"\";", {},)
      .then(res => {
        this.items = [];
        for (let i = 0; i < res.rows.length; i++) {
          let iconename = (res.rows.item(i)['Categorie'] == "Cancer") ? "medkit" : "nutrition";
          this.items.push({
            name: res.rows.item(i)['Nom'],
            icon: iconename
          });
        }
      })
      .catch(err => this.a += "not selected");
  }

  dismiss() {
    this.viewCtrl.dismiss(null);
  }

}



