import { Component } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation';
import {
  GoogleMaps, GoogleMap, GoogleMapsEvent,
  LatLng, CameraPosition, MarkerOptions, Marker, GoogleMapOptions
} from '@ionic-native/google-maps';

@Component({
  selector: 'page-localize',
  templateUrl: 'localize.html'
})

export class LocalizePage {
  latitude: number;
  longitude: number;
  map: GoogleMap;
  a: string;

  constructor(private geolocation: Geolocation, private googleMaps: GoogleMaps) {
  }

  ionViewDidLoad() {
    this.loadMap();
  }

  loadMap() {
   this.geolocation.getCurrentPosition().then((resp) => {
      this.latitude = resp.coords.latitude;
      this.longitude = resp.coords.longitude;
     this.loadMap2();
   }).catch((error) => {
      this.a = 'Error getting location';
    });

  }


    loadMap2() {
        let mapOptions: GoogleMapOptions = {
          camera: {
            target: {
              lat: this.latitude,
              lng: this.longitude
            },
            zoom: 18
          }
        };

        this.map = new GoogleMap('map', mapOptions);

        this.map.on(GoogleMapsEvent.MAP_READY).subscribe(() => {
          console.log('Map is ready!');
        });
    }
}



