import { Component } from '@angular/core';
import {SQLite, SQLiteObject} from "@ionic-native/sqlite";
import {ModalController} from "ionic-angular";

import { CategorizeListPage } from "./categorize-list";

@Component({
  selector: 'page-categorizerestaurant',
  templateUrl: 'categorizerestaurant.html',
})

export class CategorizerestaurantPage {
  a: string;
  categorie: Array<{name: string, count: number}>;
  db: SQLiteObject;

  constructor(public sqlite : SQLite, public modalCtrl: ModalController) {
    this.categorie = [];
    this.sqlite.create({
      name: 'restaurants.db',
      location: 'default'
    })
      .then(db => {
        this.db = db;
        this.getCategorie();
      });
  }

  getCategorie() {
    let sql = "SELECT DISTINCT Categorie FROM restaurant";
    this.db.executeSql(sql, {})
      .then((res) => {
        for (let i = 0; i < res.rows.length; i++) {
          this.db.executeSql("SELECT COUNT(*) as count FROM restaurant WHERE Categorie=\""+res.rows.item(i)['Categorie']+"\";", {})
            .then(result => {
              this.categorie.push({
                name :res.rows.item(i)['Categorie'],
                count : result.rows.item(0)['count']
              });
            })
            .catch(err => this.a += "not count");
        }
      })
      .catch(err => this.a += "not select");
  }

  CategorizeList(categorie) {
    let modal = this.modalCtrl.create(CategorizeListPage, {categorie: categorie});
    modal.present();

  }
}



